body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f6fa !important;
  overflow-y: scroll
}

body, html, #root{
  width: 100%;
  height: 100%;
}

:lang(jp) {
  font-family: "Sawarabi Gothic";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style: none;
}

p {
  margin: 0;
}

.p-white {
  background-color: #ffffff;
}

.header-cover{
  margin-top: 80px;
}
a:hover{
  text-decoration: none !important;
}

.header-main{
  width: 100%;
  display: block;
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e8;
  padding-top: 16px;
  padding-bottom: 16px;
  position: fixed;
  top: 0px;
  z-index: 1;
}

.hm-content-area{
  display: flex;
  width: 1400px;
  margin: 0 auto;
}

.hm-logo{
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
  border-radius: 25px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-animation: Header-logo-spin infinite 5s linear;
          animation: Header-logo-spin infinite 5s linear;
}

.hm-logo:hover{
  opacity: 0.7;
}

.hm-nav-content-area{
  line-height: 50px;
}

.hm-nav-bar{
  margin: 0;
  display: flex;
}

.hm-nav-list{
  margin-right: 40px;
  font-size: 23px;
  font-weight: bold;
}

.hm-nav-link{
  -webkit-transition: all 0.15s;
  transition: all 0.15s
}

.hm-nav-list > a > p {
  margin-bottom: 0px
}

@-webkit-keyframes Header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.footer-main{
  width: 100%;
  height: 160px;
  background-color: #313131;
  margin-top: 40px;
}

.fm-auth-list{
  width: 1400px;
  height: 120px;
  margin: 0 auto;
}

.fm-widget{
  line-height: 120px;
  font-size: 20px;
  color: #e0e0e8;
  margin-left: 50px;
  text-decoration: none !important;
}

.fm-widget:hover{
  color: #e0e0e8 !important;
}

.fm-mid-line{
  width: 100%;
  height: 2px;
  background-color: #e0e0e8;
}
.edc-Component{
  margin-top: 40px;
  border: 1px solid #e0e0e8;
  padding: 10px;
}

.edc-title{
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 0px;
  padding: 5px;
}

.eeda{
  height: 40px;
  line-height: 40px;
  display: flex;
}

.eeda-title{
  width: 230px;
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 7px;
}

.eeda-content{
  padding-left: 10px;
}
.wt-Component{
  margin-top: 40px;
  border: 1px solid #e0e0e8;
  padding: 10px;
}

.wt-title{
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 0px;
  padding: 5px;
}

.w-property{
  margin-bottom: 0px;
  line-height: 35px;
  padding-left: 10px;
}

.w-first{
  width: 140px;
}

.w-title{
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 7px;
}
.yp-Component{
  margin-top: 40px;
  border: 1px solid #e0e0e8;
  padding: 5px;
}

.yp-title{
  margin-bottom: 0px;
  font-size: 30px;
  font-weight: bold;
  padding: 10px;
}

.ypt-video-area{
  display: inline-flex;
  width: 302px;
  height: 212px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px;
  border: 1px solid #e0e0e8;
  margin: 7px;
}

.ypt-content-area{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.6);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
}

.ypt-content-area:hover{
  background-color: rgba(0,0,0,0.3)
}

.ypt-title{
  color: #ffffff
}
.main{
  width: 1400px;
  margin: 0 auto;
}

.main-body{
  width: 960px;
  margin-right: 40px;
}

.side-body{
  width: 400px;
  background-color: #ffffff;
  border: 1px solid #e0e0e8;
  margin-top: 40px;
}
.Message-Component{
  display: table-row;
  width: 100%;
  height: 100%;
}

.Message-area{
  width: 1400px;
  margin: 0 auto;
  padding-top: 40px;
}

.mt-area{
  margin-bottom: 40px;
  padding: 10px;
  background-color: #ffffff;
  border: 1px solid #e0e0e8;
}

.mt-contents{
  margin-bottom: 0.1rem
}

.mt-date{
  font-size: 17px;
  font-weight: bold;
}
.Letter-Component{
  display: table-row;
  width: 100%;
  height: 100%
}

.lt-cover{
  width: 1400px;
  margin: 0 auto;
}

.lt-first{
  margin-top: 123px;
  border: 1px solid #e0e0e8;
  padding: 10px;
}

.lt-other{
  margin-top: 40px;
  border: 1px solid #e0e0e8;
  padding: 10px;
}

.lt-title{
  font-size: 20px;
  font-weight: bold;
}

.lt-first-image{
  width: 50%;
}
.Remembrance-Component{
  display: table-row;
  width: 100%;
  height: 100%;
}

.Remembrance-area{
  width: 1400px;
  padding: 20px;
  margin: 0 auto;
  border: 1px solid #e0e0e8;
}

.rt-image{
  max-width: 31.85%;
  max-height: 33.333333%;
  margin: 10px;
  border-radius: 10px;
}

.rb-cover{
  margin-top: 120px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.application {
  display: table;
  width: 100%;
  height: 100%;
}

.app-flex {
  display: flex;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

